"use client";
import { useGetDonationsQuery } from "@/redux/api/donation";
import scss from "./DonationMainContent.module.scss";

const DonationMainContent = () => {
  const { data } = useGetDonationsQuery();
  console.log(data, "Donation");

  return (
    <div className={scss.DonationMainContent}>
      {
        data?.map((item) => (
          <div key={item.id}>
            import React from "react";
            <h1>Отправитель: {item?.amount}</h1>
            <h3>{item.amount} Сом</h3>
            <p>{item.comment}</p>
          </div>
        )) || <p>No donations found.</p>

        // Add your own content here to display the donations data. For example:
      }
    </div>
  );
};

export default DonationMainContent;
